main {
  width: min(90vw, 600px);
}

h1 {
  font-size: 2.5rem;
  text-align: center;
}

svg {
  width: 100%;
}

.tg {
  border-collapse: collapse;
  border-spacing: 0;
}
.tg td {
  border-color: black;
  border-style: solid;
  border-width: 1px;
  font-family: Arial, sans-serif;
  font-size: 14px;
  overflow: hidden;
  padding: 10px 5px;
  word-break: normal;
}
.tg th {
  border-color: black;
  border-style: solid;
  border-width: 1px;
  font-family: Arial, sans-serif;
  font-size: 14px;
  font-weight: normal;
  overflow: hidden;
  padding: 10px 5px;
  word-break: normal;
}

table {
  text-align: center;
  margin-bottom: 2rem;
  margin-top: 3rem;
  width: 100%;
}
